import React from "react";
import '../styles/components/Header.css';
import logoSvg from '../assets/logo.svg';
import searchSvg from '../assets/search.svg';
import basketSvg from '../assets/basket.svg';

const Header = () => {
    const scrollToAbout = () => {
        const aboutContent = document.getElementById("about-content");
        if (aboutContent) {
            aboutContent.scrollIntoView({ behavior: "smooth" });
        }
    };

    return (
        <div className="header">
            <img src={logoSvg} className="logo" alt="" />

            <div className="menu">
                <p className="menu-item"></p>
                <p className="menu-item" onClick={scrollToAbout} > </p>
                <p className="menu-item"></p>
            </div>
            <div className="header-icons">
                <img src={searchSvg} alt='' className="svg-icon" />
                <img src={basketSvg} alt='' className="svg-icon" />
            </div>
        </div>
    )
}

export default Header;